#home{
  background-image: url('../../assets/bg.jpg');
  background-size: cover;
  padding-top: 5vh;
  .db-connect-info{
    background-color: #ffffff4f;
    height: 10vh;
    .title{
      color: #44c924;
    }
  }
  .db-export-check{
    background-color: #ffffff4f;
    height: 13vh;
    margin-top: 2vh;
    .title{
      color: #44c924;
    }
    .btn-area{
      text-align: center;
      display: flex;
      justify-content: space-around;
      padding: 0 32vw;
      margin-top: 1vh;
    }
  }
  .db-db-info{
    background-color: #ffffff4f;
    height: 68vh;
    margin-top: 2vh;
    .title{
      color: #44c924;
    }
    .table-area{
      max-height: 65vh;
      overflow: auto;
    }
    .table-area::-webkit-scrollbar {
      /*滚动条整体样式*/
      width : 10px; /*高宽分别对应横竖滚动条的尺寸*/
      height: 1px;
    }
    .table-area::-webkit-scrollbar-thumb {
      /*滚动条里面小方块*/
      border-radius : 10px;
      background-color: skyblue;
      background-image: -webkit-linear-gradient(
                      45deg,
                      rgba(255, 255, 255, 0.2) 25%,
                      transparent 25%,
                      transparent 50%,
                      rgba(255, 255, 255, 0.2) 50%,
                      rgba(255, 255, 255, 0.2) 75%,
                      transparent 75%,
                      transparent
      );
    }
    .table-area::-webkit-scrollbar-track {
      /*滚动条里面轨道*/
      box-shadow : inset 0 0 5px rgba(0, 0, 0, 0.2);
      background : #ededed;
      border-radius: 10px;
    }
  }
}
